* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  text-align: center;
}

.app {
  background-image: url("https://imgr.search.brave.com/GrzxiRUiJTDz8OWBvlNKgxVgVxqTt5NH8UYl3U4NhvI/fit/1200/1200/ce/1/aHR0cHM6Ly9lbi53/aW5lby5kZS9tZWRp/YS9pbWFnZS83YS9j/MS9jNS81ZmRhYjFl/MGIwYTJkNDAwZjQ1/ZTk3ODJmZTBhOTU4/Mi5qcGc");
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.app.rainy {
  background-image: url("https://imgr.search.brave.com/r2VHJwC9AAal6T8fNFYUb-OaWQmKsWANSkZZwhoRYlo/fit/500/276/ce/1/aHR0cHM6Ly9tZWRp/YS5naXBoeS5jb20v/bWVkaWEvdVFONmJx/R0U2NkgyRS9naXBo/eS5naWY.gif");
}

.app.windy {
  background-image: url("https://i.pinimg.com/originals/06/0f/49/060f49910430db8130e51d73cf637e06.gif");
}

.app.foggy {
  background-image: url("https://uploads.codesandbox.io/uploads/user/6fd775a7-55ba-47ef-9a4e-2bd5d876b1e5/3x0W-77422432ef2ee5f1ffbd8828b1bca3b9.gif");
}

.app.sunny {
  background-image: url("https://thumbs.gfycat.com/AcceptableWaterloggedKiskadee-max-1mb.gif");
}

.app.snow {
  background-image: url("https://miro.medium.com/proxy/1*rtTEJt8wDT9BcYTG4pW-oQ.gif");
}

.app.cloudy {
  background-image: url("https://www.adventurebikerider.com/wp-content/uploads/2017/10/tumblr_o27c7fByaO1tchrkco1_500.gif");
}
main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.85)
  );
  padding: 2rem;
}

.search-box {
  width: 100%;
  margin: 0 0 5rem;
}
input {
  text-align: left;
}
.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 1rem;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 0px 0px 1rem 1rem;
  margin-top: -2rem;

  box-shadow: 0px 0.5rem rgba(0, 0, 0, 0.2);

  color: #000;
  font-size: 1rem;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  padding: 0rem 0rem 2rem;
}

.location-box .date {
  font-size: 1.5rem;
  font-weight: 50;
  font-style: italic;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30% auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 1.5rem;

  padding: 5% 15%;
  font-size: 400%;
  font-weight: 900;

  text-shadow: rgba(0, 0, 0, 0.5);
  box-shadow: rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  font-size: 300%;
  font-weight: 700;
  text-shadow: rgba(50, 50, 70, 0.5);
}
